.Intro{
  width: 100%;
  height: 50%;
  margin: 3% auto 7% auto;
  padding: 0 5%;
  text-align: center;
  align-content: center;
  /* min-height: 500px;  */
}


.IntroTextBox{
  margin: auto 5%;
  color: #fbc2eb;
  border-style: solid;
  border-width: 5px;
  border-color: #fbc2eb;
  padding: 2%;
  border-radius: 30px;
  font-size: 30px;
}
/* .sidebar{
  background-color: #3f609b;
  width: 100%;
} */
.Projects{
  padding: 0 5%;
  margin: 3% auto 7% auto;
  border-radius: 2px;
}
.ProjectsDiv{
  padding-left: 5%;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  padding-right: 5%;
  /* padding-top: 5%; */
}
.projectOverlay{
  background-color: #000000;
  position: fixed;
  border-radius: 5%;
  opacity: 0;
  transition: .3s ease;
  z-index: 0;
}
.ProjectCard{
  position: relative;
  margin: 2.7%;
  border-radius: 5%;
  background-color: white;
  align-content: center;
  z-index:2;
}
.CardImage{
  display: block;
  width: 100%;
  max-height: 100%;
  align-content: center;
  overflow: hidden;
  border-radius:  5%;
}
.cardCover{
  position: absolute;
  opacity: 0.7;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 5%;
  background-image: linear-gradient(to bottom, #fbc2fb,#a6c1ee);
}
.overlay{
  position: absolute;
  top:0;
  bottom: 0;
  left: 0;
  right: 0;
  border-radius: 5%;
  opacity: 0.7;
  transition: .5s ease;
  background-color: #ffffff;
}
.ProjectCard:hover .overlay{
  opacity: 0;
}
.CardText{
  color: #000000;
  font-size: 150%;
  padding-top: 45%;
  font-weight: 600;
  text-align: center;
}
.Icon{
  font-size: 2vw;
  /* vertical-align: middle; */
  /* margin: auto; */
  padding: 20%;
  /* text-align: center; */
}
.dateClass{
  text-align: start;
  font-weight: 700;
}
.title{
  font-weight: 300;
  font-family: 'Bebas Neue', cursive;
  color: #ffffff;
  text-shadow: 4px 4px #444444;
  font-size: 5vw;
}

.timelineBox{
  padding: 1px;
  width: 90%;
  margin: 3% auto 7% auto;
  /* background-image: linear-gradient(to bottom, #fbc2fb,#a6c1ee); */
  background-color: #444444;

}
.timeline{
  min-height: 500px;
  background-attachment: fixed;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  max-width: 100%;
  max-height: 100%;
  /* background-image: linear-gradient(to bottom, #fbc2fb,#a6c1ee); */
  background-color: #a8a8a8;
  margin: 5%;
}

.TechnicalProfiles{
  text-align: center;
  display: inline-block;
  padding-bottom: 5%;

}
.Profile{
  font-weight: 800;
  font-size: 20px;
}
.CoCurricular{
  padding: 0 10%;
  margin: 3% auto 7% auto;
}
@media (max-width: 575.98px) {
  .title{
    font-size: 15vw;
  }
  .Social{
    font-size: 8vw;
    width: 11vw;
  }
  .Icon{
    font-size: 4vw;
  }
 }
 @media (min-width: 992px) { 
   .Social{
     font-size: 3vw;
     width: 5vw;
   }
 }
